@media screen and (min-width: map-get($map: $screen-sizes, $key: "sm")) and (max-width: (map-get($map: $screen-sizes, $key: "md") - 1px)) {
    html {
        font-size: 20px;
    }

    /* ============================= SECTIONS ============================= */

    // sections/_landing.scss
    #logo {
    }

    #nav-items {
        height: 25%;
        flex-direction: column;
        justify-content: space-between;
        align-content: center;
        align-items: center;

        a {
        }
    }

    // sections/_suppliers.scss
    #suppliers-section {
    }

    #suppliers-container {
        grid-template-columns: 1fr 1fr;
        .supplier {
            .supplier-image {
            }

            &:hover .supplier-image {
            }

            .view-supplier-overlay {
            }

            &:hover .view-supplier-overlay {
            }

            .view-supplier-button-container {
            }

            &:hover .view-supplier-button-container {
            }
        }
    }

    // sections/_about.scss
    #about-content {
    }

    #about-section {
    }

    // sections/_contact.scss
    #contact-header-content,
    #contact-content {
        flex-direction: column;
        padding: 0px !important;

        #info,
        #hours {
            text-align: center !important;
        }

        #osso-map {
            margin-top: 30px;
            padding: 0px !important;
        }

        & > * {
            width: 100% !important;
        }
    }

    /* ============================= SHARED ============================= */

    // shared/_divider.scss
    .divider-wrapper {
        height: 25vh;
    }
}
